.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .ball {
    width: 22px;
    height: 22px;
    border-radius: 11px;
    margin: 0 10px;
    
    animation: 3s bounce ease infinite;
  }
  
  .blue {
    background-color: #4285F5;
    
  }
  
  .red {
    background-color: #EA4436;
    animation-delay: .25s;
  }
  
  .yellow {
    background-color: #FBBD06;
    animation-delay: .5s;
  }
  
  .green {
    background-color: #34A952;
    animation-delay: 0.75s;
  }
  
  @keyframes bounce {  
      50% {
          transform: translateY(25px);
      }
      
  }